'use strict';

const tbody = document.querySelector('tbody');
const tableContent = [...tbody.children];
const headers = document.querySelectorAll('th');

for (let i = 0; i < headers.length; i++) {
  headers[i].id = [i];
}

for (const head of headers) {
  head.addEventListener('click', (e) => {
    tableContent.sort((a, b) => {
      const itemA = a.cells[e.target.id].innerText;
      const itemB = b.cells[e.target.id].innerText;

      switch (e.target.innerText) {
        case 'Name':
        case 'Position':
          return itemA.localeCompare(itemB);
        case 'Age':
          return itemA - itemB;
        case 'Salary':
          return parseInt(itemA.slice(1))
          - parseInt(itemB.slice(1));
      }
    });

    return tbody.append(...tableContent);
  });
}
